.body {
  background-color: dodgerblue;
}

.Calculator {
  border: 5px solid black;
  border-radius: 25px;
  padding: 30px 30px 10px 30px;
  margin: 80px 750px 0px 750px;
  background-color: grey;
  height: 40vh;
}
.Calculator-screen {
  width: 87%;
  height: 90%;
  font-size: 2.5rem;
  height: 60px;
  border: none;
  background-color: #252525;
  color: #fff;
  text-align: right;
  padding-right: 20px;
  padding-left: 10px;
}
.Calculator-keys {
  margin-top: 15px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-row-gap: 10px;
  grid-column-gap: 10px;
  padding: 10px;
}
.Calculator-keys > button {
  background: black;
  font-size: 1.8rem;
  text-decoration: none;
  color:white;
}